<template>
  <div class="products">
    <div id="myModal" class="BACK-modal" v-show="modal">
      <!-- Modal content -->
      <div class="ModalContent">
        <div class="justify-content-center">
          <div style="margin-bottom: 0">
            <span v-html="text" class="icon-modal"></span>
            <span style="font-size: 20px">{{ msg }}</span>
            <!-- <div>
              <button class="btn btn-white" v-show="confirm">取消</button>
              <button class="btn btn-success" v-show="confirm">確定</button>
            </div> -->
          </div>
        </div>
      </div>
    </div>

    <div class="mt-3 text-right">
      <b-button
        v-b-modal.add-product
        class="btn btn-success btn-md mr-2"
        style="cursor: pointer"
        >建立新的商品</b-button
      >
      <!-- <button
        type="button"
        class="btn btn-primary font-weight-bold"
        @click="toadd()"
      >
        建立新的商品
      </button> -->
    </div>
    <table class="table table-striped mt-3">
      <thead>
        <tr class="thead-dark">
          <th scope="col" width="5%">項目</th>
          <th scope="col" width="10%">分類</th>
          <th scope="col" width="20%">產品名稱</th>
          <th scope="col" width="20%">產品圖</th>
          <th scope="col" width="10%" class="sort text-center">原價</th>
          <th scope="col" width="10%" class="sort text-center">售價</th>
          <th scope="col" width="15%" class="text-center">是否啟用</th>
          <th scope="col" width="10%" class="text-center">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in products" :key="item.id">
          <th scope="row">{{ index + 1 }}</th>
          <th scope="row">{{ item.category }}</th>
          <td>{{ item.title }}</td>
          <td>
            <img
              :src="item.imageUrl"
              style="width: 100px; height: 100px; object-fit: cover"
              alt=""
            />
          </td>
          <td class="text-center">{{ item.origin_price }}</td>
          <td class="text-center">{{ item.price }}</td>
          <td
            class="text-success text-center font-weight-bold"
            v-if="(item.enabled = true)"
          >
            啟用
          </td>
          <td class="text-secondary text-center" v-else>不啟用</td>
          <td class="text-center">
            <b-button
              v-b-modal.modal-center
              class="btn btn-outline-success btn-sm mr-2"
              @click="getProductTemp(item)"
              style="cursor: pointer"
              ><i class="fas fa-edit"></i
            ></b-button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm"
              @click="delProduct(item)"
              style="cursor: pointer"
            >
              <i class="fas fa-trash-alt"></i>
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <!--編輯商品-->
    <b-modal id="modal-center" centered title="商品內容" hide-footer>
      <form ref="form" @submit.prevent="handleSubmit">
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="title">名稱</label>
            <input
              type="text"
              id="title"
              class="form-control"
              v-model="productTemp.title"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="category">分類</label>
            <input
              type="text"
              id="category"
              class="form-control"
              v-model="productTemp.category"
            />
          </div>
        </div>
        <div class="form-group">
          <label for="content">敘述</label>
          <input
            type="text"
            id="content"
            class="form-control"
            v-model="productTemp.content"
          />
        </div>
        <div class="form-group">
          <label for="description">說明</label>
          <input
            type="text"
            id="description"
            class="form-control"
            v-model="productTemp.description"
          />
        </div>
        <div class="form-row">
          <div class="form-group col-md-4">
            <label for="origin_price">原價</label>
            <input
              type="text"
              id="origin_price"
              class="form-control"
              v-model="productTemp.origin_price"
            />
          </div>
          <div class="form-group col-md-4">
            <label for="price">售價</label>
            <input
              type="text"
              id="price"
              class="form-control"
              v-model="productTemp.price"
            />
          </div>
          <div class="form-group col-md-4">
            <label for="unit">單位</label>
            <input
              type="text"
              id="unit"
              class="form-control"
              v-model="productTemp.unit"
            />
          </div>
        </div>

        <div class="form-group">
          <label>輸入圖片網址</label>
          <input
            type="text"
            placeholder="請輸入圖片連結"
            class="form-control"
            v-model="productTemp.imageUrl[0]"
          />
        </div>
        <img :src="productTemp.imageUrl[0]" alt="" class="img-fluid" />

        <div class="form-group form-check">
          <input
            type="checkbox"
            id="exampleCheck1"
            v-model="productTemp.enabled"
          />
          <label class="form-check-label" for="exampleCheck1">是否啟用</label>
        </div>
        <hr />
        <button class="btn btn-block btn-danger" type="submit">儲存變更</button>
      </form>
    </b-modal>
    <!--編輯商品-->

    <!--新增商品-->
    <b-modal id="add-product" centered title="商品內容" hide-footer>
      <el-form
        class="form-test mb-5"
        :model="productAdd"
        :rules="rules"
        ref="productAdd"
        @submit.prevent.native="submitForm('productAdd')"
      >
        <div class="form-row">
          <div class="form-group col-md-6">
            <el-form-item label="名稱" prop="title">
              <el-input
                v-model="productAdd.title"
                clearable
                @keypress.enter.native="submitForm('productAdd')"
              ></el-input>
            </el-form-item>
          </div>
          <div class="form-group col-md-6">
            <el-form-item label="分類" prop="category">
              <el-input
                v-model="productAdd.category"
                clearable
                @keypress.enter.native="submitForm('productAdd')"
              ></el-input>
            </el-form-item>
          </div>
        </div>
        <div class="form-group">
          <el-form-item label="敘述" prop="content">
            <el-input
              v-model="productAdd.content"
              clearable
              @keypress.enter.native="submitForm('productAdd')"
            ></el-input>
          </el-form-item>
        </div>
        <div class="form-group">
          <el-form-item label="說明" prop="description">
            <el-input
              v-model="productAdd.description"
              clearable
              @keypress.enter.native="submitForm('productAdd')"
            ></el-input>
          </el-form-item>
        </div>
        <div class="form-row">
          <div class="form-group col-md-4">
            <el-form-item label="原價" prop="origin_price">
              <el-input
                v-model="productAdd.origin_price"
                clearable
                @keypress.enter.native="submitForm('productAdd')"
              ></el-input>
            </el-form-item>
          </div>
          <div class="form-group col-md-4">
            <el-form-item label="售價" prop="price">
              <el-input
                v-model="productAdd.price"
                clearable
                @keypress.enter.native="submitForm('productAdd')"
              ></el-input>
            </el-form-item>
          </div>
          <div class="form-group col-md-4">
            <el-form-item label="單位" prop="unit">
              <el-input
                v-model="productAdd.unit"
                clearable
                @keypress.enter.native="submitForm('productAdd')"
              ></el-input>
            </el-form-item>
          </div>
        </div>

        <div class="form-group">
          <el-form-item label="輸入圖片網址" prop="imageUrl">
            <el-input
              v-model="productAdd.imageUrl[0]"
              clearable
              @keypress.enter.native="submitForm('productAdd')"
            ></el-input>
          </el-form-item>
        </div>
        <img :src="productAdd.imageUrl[0]" alt="" class="img-fluid" />
        <hr />
        <button
          class="btn btn-block btn-danger"
          ref="btnSubmitForm"
          native-type="submit"
        >
          確定新增
        </button>
        <!-- <button class="btn btn-block btn-danger" type="submit">儲存變更</button> -->
      </el-form>
    </b-modal>
    <!--新增商品-->

    <pagination
      :pages="pagination"
      @update="getProducts"
      v-if="products[0]"
    ></pagination>
  </div>
</template>
  
  <script>
import pagination from "@/components/MyPagination.vue";
import axios from "axios";

export default {
  name: "MyProducts",
  data() {
    return {
      products: [],
      productTemp: {
        imageUrl: [], //這邊要包成陣列才能傳過去
      },
      pagination: {},
      modal: false,
      text: "",
      msg: "",
      productAdd: {
        title: "",
        category: "",
        content: "",
        description: "",
        imageUrl: [],
        enabled: true,
        origin_price: "",
        price: "",
        unit: "",
      },
      rules: {
        title: [{ required: true, message: "標題為必填", trigger: "blur" }],
        category: [{ required: true, message: "分類為必填", trigger: "blur" }],
        content: [{ required: true, message: "敘述為必填", trigger: "blur" }],
        description: [
          { required: true, message: "說明為必填", trigger: "blur" },
        ],
        imageUrl: [
          { required: true, message: "圖片網址為必填", trigger: "blur" },
        ],
      },
    };
  },
  components: {
    pagination,
  },
  created() {
    this.getProducts();
  },
  methods: {
    getProducts(page) {
      this.isLoading = true;
      const url = `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/ec/products?page=${page}`;
      this.$http.get(url).then((res) => {
        this.products = res.data.data;
        this.pagination = res.data.meta.pagination;
      });
    },
    getProductTemp(item) {
      this.productTemp = { ...item };
    },
    handleSubmit() {
      axios
        .patch(
          `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/admin/ec/product/${this.productTemp.id}`,
          this.productTemp
        )
        .then((res) => {
          console.log(res);
          this.text =
            '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
          this.msg = "編輯成功！";
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
          this.getProducts();
        })
        .catch((err) => {
          console.log(err);
          this.text =
            '<i class="fa-solid fa-triangle-exclamation" style="color: red;"></i>';
          this.msg = "編輯失敗，請稍後再試！";
          this.modal = true;
          this.modal = setTimeout(() => {
            this.modal = false;
          }, 1000 * 1 * 1);
        });
    },
    submitForm(form) {
      this.$refs[form].validate((valid) => {
        if (valid) {
          const order = { ...this.productAdd };
          axios
            .post(
              "https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/admin/ec/product",
              order
            )
            .then((res) => {
              console.log(res);
              this.text =
                '<i class="fa-solid fa-circle-check" style="color: green;"></i>';
              this.msg = "新增成功！";
              this.modal = true;
              this.modal = setTimeout(() => {
                this.modal = false;
              }, 1000 * 1 * 1);
              this.getProducts();
              this.productAdd = {
                title: "",
                category: "",
                content: "",
                description: "",
                imageUrl: [],
                enabled: true,
                origin_price: "",
                price: "",
                unit: "",
              };
            })
            .catch((err) => {
              console.log(err.response.data);
            });
        } else {
          return false;
        }
      });
    },
    delProduct(item) {
      const h = this.$createElement;
      this.$msgbox({
        title: "提示訊息",
        message: h("p", null, [h("span", null, "確定刪除此筆商品嗎?")]),
        showCancelButton: true,
        confirmButtonText: "確定",
        cancelButtonText: "取消",
        beforeClose: (action, instance, done) => {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "執行中...";
            axios
              .delete(
                `https://course-ec-api.hexschool.io/api/d25970ec-b87b-44f6-9785-9bdbbcd8dc56/admin/ec/product/${item.id}`
              )
              .then((res) => {
                console.log(res);
                setTimeout(() => {
                  done();
                  setTimeout(() => {
                    instance.confirmButtonLoading = false;
                  }, 300);
                }, 3000);
                this.$message({
                  type: "info",
                  message: "已刪除",
                });
                this.getProducts();
              })
              .catch((err) => {
                console.log(err);
                done();
              });
          } else {
            done();
          }
        },
      });
    },
  },
};
</script>


<style>
.modal-backdrop {
  background-color: rgba(0, 0, 0, 0.7) !important;
}

.BACK-modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
  z-index: 9999999999;
}

/* Modal Content/Box */
.ModalContent {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: #fefefe;
  /* margin: 15% auto; */
  padding: 20px;
  border: 1px solid #888;
  max-width: 100%;
}

.icon-modal {
  font-size: 25px;
  vertical-align: middle;
  margin-right: 5px;
}
</style>
  